<template>

    <h1 class="text-center mt-4">Bienvenido, calculemos tu IMC y tus calorías diarias</h1>
    
    <div class="container">
        <div class="card mt-4 d-flex">
            <div class="card-body text-center">
                <div class="form mt-2">
                    <label  for="">Peso (kg)</label>
                    <input v-model="peso" type="text" name="" placeholder="Introduce tu peso" id="">
                </div>
                <div class="form mt-2">
                    <label for="">Altura (cm)</label>
                    <input v-model="altura" type="text" name="" placeholder="Introduce tu altura" id="">
                </div>
                <div class="form mt-2">
                    <label for="">Edad</label>
                    <input v-model="edad" type="text" name="" placeholder="Introduce tu edad" id="">
                </div>
                <div class="form mt-2">
                    <label for="">Selecciona tu genero:</label>
                    <select v-model="genero" name="" id="">
                        <option value="Hombre">Hombre</option>
                        <option value="Mujer">Mujer</option>
                    </select>
                </div>
                <div class="form mt-2">
                    <label for="">Indica tu nivel de actividad</label>
                    <select v-model="factor" name="" id="">
                        <option value="1.2">Sedentario</option>
                        <option value="1.37">1-3 veces por semana</option>
                        <option value="1.46">4-5 veces por semana</option>
                        <option value="1.55">Ejercicio diario</option>
                        <option value="1.72">Ejercicio intenso</option>
                        <option value="1.9">Ejercicio muy intenso</option>
                    </select>    
                </div>
                <div class="form mt-2">
                    <label for="">Selecciona tu objetivo</label>
                    <select v-model="objetivo" name="" id="">
                        <option value="aumentar masa muscular">Aumentar masa muscular</option>
                        <option value="bajar peso">Bajar de peso</option>
                        <option value="mantener peso">Mantener peso</option>
                    </select>
                </div>
                <div class="form mt-2">
                    <label for="">Resultados</label>
                    <h3 class="h4">Tu Indice de masa corporal es : {{ImcValue}}</h3>
                    <h3 class="h4">Condicion: "{{CategoriaImc}}"</h3>
                   <h3 class="h4">Debes consumir al día {{CalDiariasReajuste}} calorías para {{objetivo}}</h3>
                </div>

            </div>

        </div>


    </div>
    
   
    
</template>


<script>
export default {

    props: {
        peso: {
            type: Number,
            default: 80
            
        },
        altura: {
            type: Number,
            default: 170
            
        },
        edad: {
            type: Number,

            
        },

        genero: {

            type: String,
            

        },
        factor: {
            type: Number,
        },
        objetivo: {
            type: String
        }
    },
    computed :{
        ImcValue(){
            var imcValue = this.peso/(this.altura*this.altura/10000);
            return Math.round(imcValue)
            //return imcValue
        },
        CategoriaImc(){
            if (this.ImcValue<25) {
                return 'Estas saludable'
            } else if (this.ImcValue >= 25 & this.ImcValue<30) {
                return 'Estas con sobrepeso'
            } else if (this.ImcValue >= 30) {
                return 'Obesidad clase 1'
            }

        },
        TmbValue() {

            if ( this.genero === "Hombre"){
                return this.peso*10+6.25*this.altura-5*this.edad+5
            } else {
                return this.peso*10+6.25*this.altura-5*this.edad-161
            }

        },
        CalDiarias(){
            return Math.round(this.TmbValue*this.factor);
        },
        CalDiariasReajuste(){
            if(this.objetivo === "aumentar masa muscular"){

                var obj025xsemana= Math.round(this.CalDiarias*1.1);
                var obj050xsemana= Math.round(this.CalDiarias*1.2);
                var obj100xsemana= Math.round(this.CalDiarias*1.39);
                let arreglo= [obj025xsemana,obj050xsemana,obj100xsemana]
                
                
                return arreglo[0]
            } else if(this.objetivo === "bajar peso") {
                var obj025xsemana= Math.round(this.CalDiarias*0.9);
                var obj050xsemana= Math.round(this.CalDiarias*0.8);
                var obj100xsemana= Math.round(this.CalDiarias*0.65);
                let arreglo= [obj025xsemana,obj050xsemana,obj100xsemana]
                return arreglo[0]
            } else if (this.objetivo === "mantener peso") {
                return this.CalDiarias
            }
        }
    }
    
}
</script>